import { getBuinessHours } from "./block";

export const popup = item => {
  const now = new Date();
  const day = now.getDay();
  const business = getBuinessHours(day, item.business_hours);

  return `
  <h1>${item.name}</h1>
  <div class="popup-info">
    <div class="address">${item.address}</div>
    <div class="business-houres"> | ${business}</div>
    <div class="phone"> | ${item.phone}</div>
    <div class="quantity">
      <div class="common adult"> ${item.adult_count}</div>
      <div class="common child"> ${item.child_count}</div>
    </div>
  </div>
  `;
};
